//https://ext.dcloud.net.cn/plugin?id=72
export default {
	name: "category",
	data() {
		return {
			height: 0,
			scrollTop: 0,
			scrollHeight: 0,
			categoryActive: 0,
			activeStyle: {
				color: this.activeTextColor,
				backgroundColor: this.activeBackgroundColor
			}
		}
	},
	props: {
		//主分类激活索引
		defaultActive: {
			type: Number,
			default: 0
		},
		//主分类List
		categoryList: {
			type: Array,
			default: () => {
				return null;
			}
		},
		//侧边分类List
		subCategoryList: {
			type: Array,
			default: () => {
				return null;
			}
		},
		activeTextColor: {
			type: String,
			default: '#F24544'
		},
		activeBackgroundColor: {
			type: String,
			default: '#e5e5e5'
		},
		label: {
			type: String,
			default: 'name'
		},
		imgSrc: {
			type: String,
			default: 'logo'
		},
		//主分类点击事件
		categoryMainClick: {},
		//子分类点击事件
		categorySubClick: {},
		rightBackgroundColor: {
			type: String,
			default: '#e5e5e5'
		},
		rightImgWidth:{
			type: String,
			default: '50rpx'
		},
		rightImgHeight:{
			type: String,
			default: '50rpx'
		},
		rightImgBorderRadius:{
			type: String,
			default: '100%'
		}
	},
	methods: {
		scroll(e) {
			this.scrollHeight = e.detail.scrollHeight;
		},
		categoryClickMain(category, index) {
			this.$emit('categoryMainClick',category)
			this.categoryActive = index;
			this.scrollTop = -this.scrollHeight * index;
		},
		categoryClickSub(category) {
			this.$emit('categorySubClick',category)
		}
	},
	mounted() {
		this.categoryActive = this.defaultActive
		uni.getSystemInfo({
			success: res => {
				this.height = res.screenHeight;
			}
		})
	},
	watch: {
		subCategoryList(newValue, oldValue) {
			
		}
	},
}
